			<div class="text-right">
				<el-pagination :current-page.sync="currentPage" :page-sizes="[50, 100, 200, 300]" :page-size.sync="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="filteredSalary.length"></el-pagination>
			</div>
			<el-table size="small" :data="filteredSalary.slice(pageStart, pageStart + pageSize)" v-loading="loading" element-loading-text="" v-loading.fullscreen="true" stripe max-height="640">
				<el-table-column type="index" width="50" align="center"></el-table-column>
				<el-table-column prop="CompanyName" label=""></el-table-column>
				<el-table-column prop="Category" label="" align="center"></el-table-column>
				<el-table-column prop="JobTitle" label=""></el-table-column>
				<el-table-column prop="Salary" label="" align="right"></el-table-column>
