				<el-table-column prop="PublishDate" label="" align="center"></el-table-column>
				<el-table-column label="" align="center">
					<template slot-scope="scope">
						<el-link type="primary" :underline="false" @click="showDetail(scope.row)"></el-link>
					</template>
				</el-table-column>
			</el-table>
			<div class="text-right">
				<el-pagination :current-page.sync="currentPage" :page-sizes="[50, 100, 200, 300]" :page-size.sync="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="filteredSalary.length"></el-pagination>
			</div>
			<el-dialog :title="detail.CompanyName" :visible.sync="dialogVisible" width="40%">
				<el-form ref="form">
					<el-form-item label="">
